import React from 'react';
import OneSvg from "../svg-numbers/OneSvg"

// images
import list2 from "../../../../static/images/slide3/chapter-3/list-2.jpg"
import list3 from "../../../../static/images/slide3/chapter-3/list-3.jpg"
import list4 from "../../../../static/images/slide3/chapter-3/list-4.jpg"
import list5 from "../../../../static/images/slide3/chapter-3/list-5.jpg"
import list6 from "../../../../static/images/slide3/chapter-3/list-6.jpg"
import TwoSvg from "../svg-numbers/TwoSvg"
import ThreeSvg from "../svg-numbers/ThreeSvg"
import FourSvg from "../svg-numbers/FourSvg"
import FiveSvg from "../svg-numbers/FiveSvg"
import SixSvg from "../svg-numbers/SixSvg"

const S3Ch3List = (props) => {
	return (
		<div className="slide-content__section s3-ch3__list">
			<div className="item--custom item--1">
				<div className="item__content">
					<h3 className="reveal--fade">Перечень предлагаемых услуг</h3>
					
					<OneSvg extraClass="reveal--to-left" />
					
					<p className="reveal--to-left">
						Техническое обслуживание инженерных коммуникаций и систем жизнеобеспечения объекта, эксплуатация наружных
						сетей и коммуникаций (электроснабжение, теплоснабжение, водоснабжение, водоотведение, вентиляция и
						кондиционирование, СКС, комплекс АППЗ, СКУД, видеонаблюдение, диспетчеризация)
					</p>
				</div>
			</div>
			
			
			<div className="item item--2 reveal--fade">
				<div className="item__img">
					<img src={ list2 } alt="Изображение" />
				</div>
				
				<div className="item__text">
					<p>
						Текущие ремонтные работы оборудования (отдельные узлы, инженерные конструкции), конструктивных элементов
						объекта
					</p>
				</div>
				
				<TwoSvg />
			</div>
			
			<div className="item item--3 item--alt reveal--fade">
				<div className="item__img">
					<img src={ list3 } alt="Изображение" />
				</div>
				
				<div className="item__text">
					<p>
						Услуги круглосуточной аварийно-диспетчерской службы;
					</p>
				</div>
				
				<ThreeSvg />
			</div>
			
			<div className="item item--4 reveal--fade">
				<div className="item__img">
					<img src={ list4 } alt="Изображение" />
				</div>
				
				<div className="item__text">
					<p>
						Подбор, обучение и аттестация персонала
					</p>
				</div>
				
				<FourSvg />
			</div>
			
			<div className="item item--5 item--alt reveal--fade">
				<div className="item__img">
					<img src={ list5 } alt="Изображение" />
				</div>
				
				<div className="item__text">
					<p>
						Проведение ежегодного технического аудита объекта, ведение документации, выполнение проектных работ
					</p>
				</div>
				
				<FiveSvg />
			</div>
			
			<div className="item item--6 reveal--fade">
				<div className="item__img">
					<img src={ list6 } alt="Изображение" />
				</div>
				
				<div className="item__text">
					<p>
						Организация проживания персонала временных жилых поселков на строительных объектах, бытовое обслуживание персонала
					</p>
				</div>
				
				<SixSvg />
			</div>
		</div>
	);
};

export default S3Ch3List;
