import React from 'react';

import articleImage from "../../../../static/images/slide3/chapter-1/article.jpg"

const S3Ch1Section2 = (props) => {
	return (
		<div
			className="slide-content__section s3-ch1__article reveal--to-left"
		>
			<div className="s3-ch1__article-image">
				<img src={ articleImage } alt="Group" />
			</div>
			
			<div className="s3-ch1__article-text">
				<p>
					Рацион питания разрабатывается индивидуально для каждого заказчика с учетом специфики условий на объекте. Особое внимание в компании уделяется подбору и обучению персонала для поддержания высокого уровня качества услуг.
				</p>
				
				<p>
					Деятельность компании по организации питания осуществляется строго с соблюдением законодательных норм РФ (СП, СанПиН, ГОСТ и др.)
				</p>
			</div>
		</div>
	);
};

export default S3Ch1Section2;
