import React from 'react';

import mapImg from "../../../../static/images/slide3/chapter-1/map.svg"

const S3Ch1Section3 = (props) => {
	return (
		<div className="slide-content__section s3-ch1__experience">
			<div className="article-blue__wrapper">
				<div className="article-blue reveal--to-left">
					<p>
						Мы обладаем опытом оказания услуг по организации питания в столовых временных жилых поселков и в местах производства работ на объектах строительства в труднодоступных регионах
					</p>
				</div>
			</div>
			
			<div className="article-map">
				<div className="map-img">
					<img className="reveal--to-left" src={ mapImg } alt="Map" />
				</div>
				
				<p className="reveal--to-left">
					Логистика доставки продовольственной продукции налажена в большинстве регионов России, в том числе: в Московской, Ленинградской, Самарской, Оренбургской, Иркутской областях, ЯНАО, ХМАО и Республике Коми, Республике Якутия, Краснодарском и Хабаровском краях.
				</p>
			</div>
		</div>
	);
};

export default S3Ch1Section3;
