import React from 'react';
import { connect } from "react-redux"

// images
import dottedRussiaMap from "../../static/images/slide1/dotted-map-of-russia-1.svg"
import gazpromLogo from "../../static/images/slide1/gazprom-logo.svg"
import gspService from "../../static/images/slide1/gsp-service.svg"
import gazpromPitanie from "../../static/images/slide1/gaspropm-pitanie.svg"
import stroiProektService from "../../static/images/slide1/stroi-proekt-service.svg"
import gazStroiProm from "../../static/images/slide1/gsp-service.svg"
import lenGazSpecStroi from "../../static/images/slide1/lgss.svg"
import stroiGazMontazh from "../../static/images/slide1/stroi-gaz-montash.svg"
import yamalTransStroi from "../../static/images/slide1/yamal-trans-stroi.svg"

const Slide1 = (props) => {
	
	const activeClassName = (props.appUi.currentSlideId === props.slideIndex) ? " slide--current" : " "
	
	return (
		<div
			className={
				"slide-content__slide content__slide-1" + activeClassName
			}
		>
			{/*SECTION 1*/ }
			<div className="slide-content__section dark-ocean-bg slide-1--s1">
				<h2 className="reveal--fade">Направления деятельности <br /> компании</h2>
				
				<ul className="custom-ul">
					<li className="reveal--to-left">{ props.slidesData.slide1.section1.list1.item1 }</li>
					<li className="reveal--to-left">{ props.slidesData.slide1.section1.list1.item2 }</li>
					<li className="reveal--to-left">{ props.slidesData.slide1.section1.list1.item3 }</li>
					<li className="reveal--to-left">{ props.slidesData.slide1.section1.list1.item4 }</li>
				</ul>
			</div>
			
			{/*SECTION 2*/ }
			<div className="slide-content__section slide-1--s2">
				<div className="content__image-wrapper">
					<img className="reveal--to-left" src={ dottedRussiaMap } alt="Карта России" />
				</div>
				
				<div className="content__text">
					<p className="reveal--to-left">Мы предоставляем услуги своим клиентам на всей территории Страны, в том числе в удаленных труднодоступных районах Крайнего Севера и приравненным к ним местностям.</p>
				</div>
			</div>
			
			{/*SECTION 3 -- GRID?*/ }
			<div className="slide-content__section slide-1--s3 dark-ocean-bg">
				<div className="grid-child grid-child--1">
					<h2 className="reveal--to-left">Качество нашего сервиса подтверждается доверием клиентов</h2>
				</div>
				<div className="grid-child sgs-blue-bg reveal--fade">
					<img src={ gazpromLogo } alt="ПАО «Газпром»" />
				</div>
				<div className="grid-child sgs-blue-bg ">
					<img src={ gspService } alt="ООО «ГСП-сервис»" />
				</div>
				<div className="grid-child sgs-blue-bg reveal--fade">
					<img src={ gazpromPitanie } alt="ООО «Газпром питание»" />
				</div>
				<div className="grid-child reveal--fade">
					<img src={ stroiProektService } alt="ООО «СтройПроектСервис»" />
				</div>
				<div className="grid-child reveal--fade">
					<img src={ gazStroiProm } alt="АО «Газстройпром»" />
				</div>
				<div className="grid-child reveal--fade">
					<img src={ lenGazSpecStroi } alt="АО «Ленгазспецстрой»" />
				</div>
				<div className="grid-child reveal--fade">
					<img src={ stroiGazMontazh } alt="ООО «Стройгазмонтаж»" />
				</div>
				<div className="grid-child sgs-blue-bg reveal--fade">
					<img src={ yamalTransStroi } alt="АО «Ямалтрансстрой»" />
				</div>
			</div>
		</div>
	)
};

const mapStateToProps = (state) => ({
	...state
})

export default connect(mapStateToProps)(Slide1);
